import React, { Component } from 'react'
import Axios from 'axios'
import '../App.css'
import { concatSeries } from 'async';

class Body extends Component {
    constructor(props) {
        super(props);
        this.state = {
            emailid : "",
            isValid: false
        };
    }

    handleChange = (e) => {
        console.log(e.target.value);
        if(this.validateEmail(e.target.value)){
            console.log('valid email')
            this.setState({
                isValid: true
            })
        }
        else{
            console.log('Invalid email')
            this.setState({
                isValid: false
            })
        }
    }

    handleClick = (e) => {
        e.preventDefault();
        console.log('clicked');
        this.setState({
            emailid: document.getElementById('emailid').value
            }, () => {
                console.log('after setstate: ', this.state.emailid);
                this.handleInsert();
        });   
        
    }

    validateEmail(email){
        var pattern = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);
        if (!pattern.test(email)) {
            return false;
        }
        return true;
    }

    handleInsert() {
        console.log('Insert Handling');
        Axios.post("http://localhost:3001/api/insert", {
            emailid: this.state.emailid
        }).then(() => {
            alert("Inserted Successfully");
        })
    }

    render() {
        return (
            <div className="p">
                <div className="txt">
                    <i>Preparing for interviews? </i>
                    Subscribe to this newsletter and
                    get daily practice problems to get <b>One Step Closer</b>
                </div>
                <label>
                    Email:
                    <input id="emailid" type="email" name="name" onChange={this.handleChange} placeholder="Enter your Email Id..." />
                </label>
                <span id="email_valid" style={{display:!this.state.isValid ? 'none': 'inline'}}>Email Address valid</span>
                <span id="email_not_valid" style={{display:this.state.isValid ? 'none': 'inline'}}>Email Address not valid</span>
                <br />
                <button onClick={this.handleClick} disabled={!this.state.isValid}>Subscribe Now!</button>
            </div>
        )
    }
}

export default Body;